	import {
			mapMutations
	} from 'vuex';
  export default {
    data(){
      return{
        modalShow: false
      }
		},
		props:{
			text: String
		},
    methods: {
			...mapMutations(['clearClip']),
      showModal(e) {
				this.modalShow = true
			},
			hideModal(e) {
				this.modalShow = false
				this.clearClip();
			},
			navToList() {
				uni.navigateTo({
						url: `/pages/product/goods-list?key=${this.text}`
				});
				this.hideModal()
			}
    },
  }
